@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Maven Pro", sans-serif;
}

body {
    background-color: #f4f4f4;
}

header {
    nav {
        padding: 1rem 0;
        box-shadow: 0px 1px 10px 0px rgb(170, 170, 170);
        background: #10002B;
        background-color: #161a1d;

        .nav-container {
            width: 60%;
            display: flex;
            justify-content: space-between;
            margin: 0 auto;
            align-items: center;

            img {
                padding: 0.5rem 0;
                width: 62.5px;
                user-select: none;
            }

            button {
                color: #fff;
                background-color: #ba181b;
                outline: none;
                border: 2px solid transparent;
                padding: 0.75rem 1.5rem;
                font-size: 1rem;
                font-weight: 600;
                cursor: pointer;
                transition: .175s ease-in-out;
                user-select: none;

                &:hover {
                    background-color: #660708;
                    color: #fff;
                }
            }
        }
    }

    h1 {
        text-align: center;
        width: 80%;
        margin: 10vh auto 8.5vh;
        font-weight: 700;
        font-size: 4.25rem;
        word-spacing: 8px;
        color: #ba181b;
        user-select: none;
    }
}

@media (max-width: 550px) {
    header {
        nav {
            .nav-container {
                width: 82.5%;

                img {
                    width: 50px;
                }

                button {
                    padding: 0.625rem 1.1rem;
                    font-size: 0.875rem;
                    font-weight: 700;
                }
            }
        }

        h1 {
            margin: 5.5vh auto 11vh;
            font-size: 3.2rem;
        }
    }
}